* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  top: 0;
  position: sticky;
  display: flex;
  padding: 0px 10px;
  background-color: #032158;
  justify-content: space-between;
  z-index: 99;
}

.navbar .nav-logo {
  display: inline-grid;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.services-clone {
  border: 1px solid #041432;
  width: 140px;
  height: 90px;
  margin: auto;
  visibility: hidden;
}

.services-clone img {
  color: white;
}

.navbar,
.navbar-nav,
.collapse {
  display: flex;
  text-decoration: none;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
}

.navbar-nav .closebtn {
  position: absolute;
  top: 0;
  right: 30px;
  font-size: 35px;
  margin-left: 50px;
  text-decoration: none;
  color: white;
  display: none;
}

.navbar-toggler {
  display: none;
}

.navbar-brand {
  cursor: pointer;
  display: flex;
  margin: auto;
}

.nav-link,
.nav-item,
.dropdown-item {
  text-decoration: none;
  list-style: none;
  padding: 5px;
  color: #fff;
}

.navbar .nav-logo .navbar-brand,
.navbar .navbar-nav .nav-item,
.navbar .navbar-nav .nav-item .dropdown-menu .dropdown-item {
  cursor: pointer;
}

