}

.navbar-dark .navbar-nav .nav-link {
  color: #9bbeff;
} */

.mr-auto {
  margin: auto;
}

.nav-item:hover,
.nav-link:hover,
.dropdown-item:hover {
  color: purple;
  background-color: aquamarine;
  border-radius: 4px;
  cursor: pointer;
}

/*each dropdown css */
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-menu {
  display: none;
  position: absolute;
  min-width: 200px;
  z-index: 1;
  background-color: #f9f9f9;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.dropdown-menu .dropdown-item {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.dropdown-menu .dropdown-item:hover {
  background-color: #f1f1f1;
}

.navbar-collapse .d-flex .btn-primary {
  text-decoration: none;
  color: white;
  background-color: blue;
  padding: 8px 12px;
  margin: 5px;
  outline: none;
  border-radius: 5px;
}

.d-flex .btn-primary:hover {
  background-color: #131387;
  color: burlywood;
}

.form-inline,
.d-flex {
  margin: auto;
}

.form-inline .btn {
  outline: none;
  padding: 12px;
  border-radius: 15px;
  cursor: pointer;
  background-color: yellowgreen;
}
.form-inline .btn:hover {
  background-color: green;
  color: white;
}

.form-control {
  margin: 10px;
  outline: none;
  padding: 12px 30px;
  border-radius: 20px;
}

/* Social Media Section */

.nav-socialmedia {
  margin: auto;
}

.media-flex a {
  cursor: pointer;
}

.navbar .nav-socialmedia .media-flex a img {
  height: 28px;
  width: 28px;
  margin: 5px;
  cursor: pointer;
  border-radius: 5px;
}
