/* Home Section */
/* Carousel Section for Home */
.Caroul-flex {
  justify-content: center;
  display: flex;
}

/* Post Section inside of Home */
.post-animate {
  position: absolute;
  animation: postmove 35s infinite;
  transition: linear;
  overflow: hidden;
  display: inline-block;
  z-index: 99;
  top: 0%;
}

.post-animate p {
  color: yellowgreen;
  font-weight: bold;
  text-decoration: none;
}

/* if user move on nes post than animatiion will be stoped and if remove mouse pointer than auto start */
.post-animate:hover {
  animation-play-state: paused;
}

@keyframes postmove {
  from {
    left: 95%;
  }
  to {
    left: -30%;
  }
}

/* About section */
.about-d-flex {
  margin: 59px 30px;
  padding: 69px;
  box-shadow: rgb(10px 15px 16px 25px);
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.about-d-flex h1 {
  padding-bottom: 10px;
  border-bottom: 2px dashed greenyellow;
  margin: auto;
  display: inline-block;
}

.about-d-flex span {
  font-weight: bold;
  justify-content: right;
  display: flex;
  margin-bottom: 20px;
}

.about-d-flex p {
  margin: 20px 0px;
}

/* Alert Section */
.alert-success {
  color: #0f5132;
  background-color: #d1e7dd;
  border-color: #badbcc;
}

.alert-dismissible {
  padding-right: 3rem;
}

.alert {
  position: relative;
  padding: 1rem 1rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}

.fade {
  transition: opacity 0.15s linear;
}

.alert-danger {
  color: #842029;
  background-color: #f8d7da;
  border-color: #f5c2c7;
}

.alert-dismissible {
  padding-right: 3rem;
}

/* Infinite Scroll Spinner Loading */
.infinite-scroll-component .text-center {
