  background-color: #5f9ea0;
  border: none;
  height: 87px;
  width: 17px;
  border-radius: 10px;
  display: none;
}

/* Media Query */
@media (max-width: 670px) {
  .services-clone {
    display: none;
  }

  .navbar {
    justify-content: space-between;
  }

  .navbar-nav {
    flex-direction: column;
    background-color: #40740b;
    position: absolute;
    right: 0;
    top: 40px;
    width: 230px;
    padding-top: 8%;
    transform: translateX(100%);
    transition: all 0.4s ease-in-out;
  }

  .nav-item {
    margin: 5% 0px;
    font-size: 20px;
  }

  .navbar-nav .closebtn {
    display: none;
  }

  .navbar-nav .closebtnOpen {
    display: block;
  }

  .navbar .openbtn {
    display: block;
  }

  .collapseNav {
    transform: translateX(0%);
  }

  .nav-socialmedia {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: cadetblue;
    width: 38%;
    height: 270px;
    display: flex;
    padding: 10px;
    transform: translateX(-100%);
    transition: all 0.4s ease-in-out;
  }

  .socialmedia-close {
    transform: translateX(0%);
  }

  .media-flex {
    flex-direction: column;
    display: flex;
    position: absolute;
  }

  .media-flex a {
    margin: 5px 0;
  }

  .OpenArrow,
  .CloseArrow {
    display: block;
  }

  .OpenArrow-btn {
    transform: translateX(100%);
  }

  .CloseArrow-btn {
    transform: translateX(0%);
  }

  .home-container .footer {
    margin: auto;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 25px;
  }

  .footer .footer-social {
    padding: 0;
    align-items: center;
    justify-content: center;
    margin: auto;
  }
  .footer .footer-social,
  .footer-social .site,
  .footer .footer-menu {
    font-size: 12px;
    border: none;
    text-align: center;
  }
  .footer-social .site {
    border-bottom: 1px dashed green;
    padding-bottom: 10px;
    margin-bottom: 5px;
  }
}
